@import url('./assets/fonts/fonts.css');

*, :before, :after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  font-size: 62.5%;
  background-color: #ededed;
  font-style: inherit;
  font-weight: inherit;
  vertical-align: baseline;
  overflow: hidden;
  color: rgba(0,0,0,0.87);
}

button, select, html, input {
  font-family: 'Roboto',sans-serif;
}

body, #root {
  background-color: #fafafa;
  color: rgba(0,0,0,.987);
  font-family: 'Roboto',sans-serif;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: .01em;
  line-height: 16px;
  overflow: auto;
  font-style: inherit;
  vertical-align: baseline;
}

html, body, #root {
  position: relative;
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
  min-height: 100%;
  height: 100%;
}

div, span, applet, object, iframe {
  font-family: inherit;
  font-size: 100%;
  font-style: inherit;
  font-weight: inherit;
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

blockquote, pre, a, abbr, acronym, address, big, cite {
  border: 0;
  font-family: inherit;
  font-size: 100%;
  font-style: inherit;
  font-weight: inherit;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

a {
  color: #3367d6;
  text-decoration: none;
  cursor: pointer;
}

input[type=text] {
  background: #fff;
  border: 1.1px solid #d3d3d3;
  border-radius: 1px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 400;
}

.flex {
  -ms-flex: 1 1;
      flex: 1 1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.flex-none {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.layout, .layout-column, .layout-row {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
}

.layout-column{
  -ms-flex-direction: column;
      flex-direction: column;
}

.layout-row {
  -ms-flex-direction: row;
      flex-direction: row;
}

.layout-column > .flex {
  min-height: 0;
}



